@import '../../../../styles/variables';

.c2md-customer-says-container {

    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #f7fafb63;
    padding: 4rem 0px;
    padding-bottom: 6rem;

    @media screen and (max-width:$mobileScreen) {

        padding: 1rem 0px;
        padding-bottom: 2rem;
    }

    .customer-says-title {

        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;




        h2 {
            color: #0C1E4E;
            letter-spacing: 0px;
            font: normal normal 600 35px/35px IBM Plex Sans;

            @media screen and (max-width:$mobileScreen) {

                font-size: 25px;
            }
        }

        .customer-says-underline {

            border-bottom: 4px solid #208CE1;
            width: 57px;
            height: 2px;
        }
    }

    .customer-says-content {

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin-top: 4rem;


        @media screen and (max-width:$mobileScreen) {

            flex-direction: column-reverse;
            margin-top: 1rem;
        }

        &_left {

            margin-right: 8rem;
            animation: fade-in 1s ease;

            @media screen and (max-width:$mobileScreen) {

                margin-right: 0rem;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }

            .comment-container {

                @media screen and (max-width:$mobileScreen) {

                    width: 95%;
                }

                .comment {

                    max-width: 450px;
                    color: #0C1E4E;
                    opacity: 1;
                    font-size: 30px;
                    font-weight: 300;
                    text-align: start;


                    @media screen and (max-width:$mobileScreen) {

                        font-size: 20px;
                        text-align: center;
                    }
                }

                .logo-top {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: flex-start;
                    margin-bottom: 15px;
                }

                .logo-bottom {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: flex-end;
                    margin-top: -10px;

                    @media screen and (max-width:$mobileScreen) {

                        margin-top: 10px;
                    }
                }
            }

            .comment-footer {

                @media screen and (max-width:$mobileScreen) {

                    width: 92%;
                }

                .name {

                    font-size: 20px;
                    font-weight: 600;
                    color: #0C1E4E;

                }

                .location {

                    font-size: 16px;
                    font-weight: normal;
                    color: #001675;

                }

                .rating-container {

                    margin-top: 0px;
                }
            }
        }

        &_right {

            @media screen and (max-width:$mobileScreen) {

                width: 100%;
            }

            &_image_container {
                width: 35vw;
                height: 400px;
                position: relative;


                @media screen and (max-width:$mobileScreen) {

                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                }

                .image-layer {

                    position: absolute;
                    background-color: transparent;
                    animation: zoom-out 1s ease;

                    .customer-says-content_right_note {
                        width: 100%;
                        text-align: center;
                        margin-top: 18px;
                    }

                }


                .image-layer-under {

                    z-index: 1;
                    bottom: 20px;
                    left: 0px;

                }

                .image-layer-middle {

                    // background-color: #e15d20;
                    z-index: 2;

                    img {
                        height: 100%;
                        width: fit-content;
                        max-width: 446px;
                        max-height: 446px;
                        margin-left: 10px;


                        @media screen and (max-width:$mobileScreen) {

                            max-width: 316px;
                        }
                    }




                }

                .image-layer-top {

                    // background-color: #20e12a;
                    z-index: 3;
                    width: 100%;
                    height: 100%;

                    .star-vector {

                        position: absolute;
                        left: 0px;
                        top: 25px;

                    }

                    .like-vector {

                        position: absolute;
                        left: 377px;
                        top: -18px;
                    }

                    .heart-vector {

                        position: absolute;
                        left: 339px;
                        bottom: -69px;
                    }
                }
            }
        }
    }

    .customer-says-slider-dot {

        padding: 0px;
        list-style: none;
        display: flex;
        flex-direction: row;
        margin-top: 3rem;


        li {
            height: 10px;
            width: 10px;
            border-radius: 100px;
            background: #D8D8D8 0% 0% no-repeat padding-box;
            margin-right: .7rem;
            cursor: pointer !important;
        }

        .active {
            background-color: #208CE1;
        }

    }
}

@keyframes zoom-out {
    0% {
        transform: scale(1.2, 1.2);
    }

    100% {
        transform: scale(1, 1);
    }

}

@keyframes fade-in {
    0% {
        // transform: scale(.9, .9);
        opacity: 0;
    }

    100% {
        // transform: scale(1, 1);
        opacity: 1;
    }

}